
import { defineComponent, ref } from 'vue'
import { useStore } from 'vuex';

export default defineComponent({
  name: 'ProjectGrid',
  setup() {
    const store = useStore();
    const projects = ref(store.state.projects)
    const baseUrl = ref(store.state.baseUrl)
    return {
      projects,
      baseUrl
    }
  },
})
